/* Default styles for larger screens */
.nav_bar {
  align-items: center;
  background-color: #131921;
  justify-content: center;
  color: #fff;
  display: flex;
  flex-direction: row;
  height: 60px;
  position: sticky;
  margin: auto;
  width: -webkit-fill-available;
  top: 0;
  z-index: 1000;
}

.navbar_logo img {
  width: 100px;
  object-fit: contain;
  margin: 0 20px;
  margin-top: 18px;
}

.address .line1 {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.location {
  height: 20px;
}

.navbar_nav {
  display: flex;
  flex-shrink: 0;
  flex-wrap: wrap;
  margin-right: 80px;
  margin-left: 40px;
  flex-grow: 0;
}

.navbar_option {
  display: flex;
  flex-direction: column;
  margin-left: 20px;
}

.navbar_opt {
  display: flex;
  align-items: center;
}

.searchbox {
  margin-left: 20px;
  margin-right: 15px;
  width: 50vw;
  align-items: center;
}

.searchbox input {
  width: 100%;
  height: 40px;
  border-radius: 10px 0px 0px 10px;
}

.navbar_search {
  background-color: #cd9842;
  border-radius: 0px 10px 10px 0px;
  color: black;
  height: 38px !important;
  width: 30px !important;
}

.line1 {
  font-size: 10px;
}

.line2 {
  font-size: 13px;
  font-weight: 700;
}

.flag {
  object-fit: contain;
  width: 30px;
}

.navbar_cart {
  margin-left: 10px;
  height: 40px !important;
  width: 40px !important;
  color: white;
}

.navbar_cart_count {
  color: white;
  font-size: medium;
}

Link {
  text-decoration: none;
}

/* Media query for screens with a maximum width of 768px */
@media screen and (max-width: 1000px) {
  .nav_bar {
    width: 100%;
    align-items: center;
    background-color: #131921;
    justify-content: center;
    color: #fff;
    display: flex;
    flex-direction: row;
    height: 60px;
    position: sticky;
    top: 0;
    margin: 0;
    z-index: 1000;
  }
  .navbar_logo img {
    width: 80px;
    margin: 0 10px;
    margin-top: 18px;
  }
  .navbar_logo {
    position: absolute;
    left: 0px;
  }
  .navbar_nav {
    display: flex;
    position: absolute;
    right: -50px;
  }
  .hide {
    display: none;
  }
}
